import React from 'react'
import Link from 'gatsby-link'
import styled, { keyframes } from 'styled-components'
import Empty from '../assets/cursor/empty.svg'
import Filled from '../assets/cursor/filled.svg'
import {start, Start, rainbow, StyledText, Content, ContentGroup, Parent, Sub, Body, StyledLink, Glow, BodyImage} from '../components/styles.js'

class Friends extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      hover1: false,
      hover2: false,
      hover3: false,
      hover4: false
    }
  }

  mouseOver = (p) => {
    this.setState({[p]: true})
  }

  mouseOut = (p) => {
    this.setState({[p]: false})
  }

  render() {
    return (
      <div>
        <Start>
          <Content>
            <ContentGroup>
              <p>
                <Glow>
                Friends
                </Glow>

                <Sub>
                Aug 2019 - Present
                </Sub>

                <Body>
                Friends is an ongoing photographic series that I began at the end of Summer 2019.
                I aimed to depict a couple of my friends in surreal situations spanning the two polar ends of a spectrum of ecstacy.
                A stylistic melting pot, the resultant photographs intend to capture simultaneously the feelings of euphoric drugged-out bliss and bleak apocalypse.
                </Body>

                <BodyImage>
                <img src={require('../assets/images/friends1.jpg')} />
                <img src={require('../assets/images/friends2.jpg')} />
                <img src={require('../assets/images/friends3.jpg')} />
                <img src={require('../assets/images/friends4.jpg')} />
                <img src={require('../assets/images/friends5.jpg')} />
                <img src={require('../assets/images/friends6.jpg')} />
                <img src={require('../assets/images/friends7.jpg')} />
                </BodyImage>
              </p>
            </ContentGroup>
          </Content>
        </Start>
      </div>
    )
  }
}

export default Friends



